<template>
	<div class="loader-row" v-if="LOADING_ON">
		<ul class="loader">
			<li class="loader-inner-1"></li>
			<li class="loader-inner-2"></li>
			<li class="loader-inner-3"></li>
		</ul>
	</div>
</template>

<script>
	import { mapGetters } from 'vuex'
  export default {
		data() {
		  return {
		    loading: false,
		  }
		},
	  computed: {
		  ...mapGetters(['LOADING_ON']),
	  }
  }
</script>

<style lang="scss">
	.loader-row {
		position: fixed;
		transform: translateX(-50%);
		left: 50%;
		bottom: 78px;
		z-index: 100;
	}

	@mixin anim() {

	}
	$delay-1: 50ms;
	$delay-2: 250ms;
	$delay-3: 500ms;
	$delay: $delay-1, $delay-2,$delay-3;
	.loader {
		display: flex;
		justify-content: space-between;
		width: calc(10px * 3 + 5px);
		@for $i from 1 through 3 {
			&-inner-#{$i} {
				width: 10px;
				height: 10px;
				background: #333;
				border-radius: 50%;
				animation: loading 1s infinite linear;
				animation-delay: nth($delay, $i);
				transition: .5s ease;
				/*&.loading {*/
				/*}*/
				/*&.loaded-hide {*/
					/*transition: .3s opacity;*/
					/*opacity: 0;*/
				/*}*/

			}
		}
	}
	@keyframes loading {
		0%, 100% {
			transform: scale(1);
			opacity: 1;
			background: #fff;
		}
		50% {
			transform: scale(.5);
			opacity: .6;
		}
	}

</style>