import Vue from "vue";
import Vuex from "vuex";
import axios from "axios";

Vue.use(Vuex);

export default new Vuex.Store({
  state: {
    lockInput: true,
    placeholder: "Ваше сообщение",
    loading: false,
    name: null,
    messages: [],
    anchor_global: "",
    user_id: "777-000987",
    answer: null,
    method: "main",
    anchor: "cinema",
    action: "",
    role: ""
  },
  mutations: {
    SET_VARIABLE(state, variable) {
      state[variable.name] = variable.value;
      state.variable = variable.name;
    },
    NEXT_MESSAGE_URL(state, [anchor, action]) {
      state[action] = anchor;
      state.action = action;
      if (action === "anchor") {
        state.anchor = anchor;
      } else if (action === "anchor_global") {
        state.anchor_global = anchor;
        state.role = state.anchor_global;
      }
    },
    SET_ANCHOR(state, id) {
      state.anchor = id;
    },
    SET_MESSAGES_TO_STATE(state, message) {
      message.visible = false;
      if (state.messages.length < 1) {
        state.messages.push(message);
      } else if (!state.messages.includes(message)) {
        state.messages.push(message);
      }
    },
    LOCK_INPUT(state, value) {
      if (state.loading) {
        state.lockInput = true;
      }
      state.lockInput = value;
    },
    SET_ANSWER(state, [index, answer]) {
      Vue.set(state.messages[index], "answer", answer);
    },
    SET_NAME(state, name) {
      state.name = name;
    },
    LOADER_SHOW(state) {
      state.loading = true;
    },
    LOADER_HIDE(state) {
      state.loading = false;
    }
  },
  actions: {
    setAnswer({ commit, dispatch, state }, [screen, answer]) {
      state.messages.forEach((msg, index) => {
        if (msg.id === screen.id) {
          commit("SET_ANSWER", [index, answer]);
          dispatch("scroll");
        }
      });
    },
    setVariable({ commit }, variable) {
      commit("LOCK_INPUT", true);
      commit("SET_VARIABLE", variable);
    },
    nextMessage({ commit, dispatch }, data = []) {
      let [screen, variable, answer, anchor, action] = data;
      commit("LOADER_SHOW");
      commit("NEXT_MESSAGE_URL", [anchor, action]);

      dispatch("setAnswer", [screen, answer]);
      setTimeout(
        () => dispatch("DOWNLOAD_MESSAGE_FROM_API", [anchor, action]),
        1000
      );
      if (variable.name) {
        commit("SET_VARIABLE", variable);
        commit("LOCK_INPUT", true);
      }
    },
    DOWNLOAD_MESSAGE_FROM_API(
      { commit, dispatch, state },
      [anchor = state.anchor, action = "anchor"]
    ) {
      commit("NEXT_MESSAGE_URL", [anchor, action]);
      let url = `https://crm-pro.ru/new/sbc/type/vk/get_data.php?method=main&id=${state.user_id}&p=${state[action]}`;
      if (state.role && state.action === "anchor") {
        url = `https://crm-pro.ru/new/sbc/type/vk/get_data.php?method=page&id=${state.user_id}&p=${state.role}&l=${anchor}`;
      }

      return axios(url, {
        method: "GET"
      })
        .then(message => {
          let msg = message.data;
          Vue.set(msg, "answer", null);

          if (msg.variable.name) {
            commit("LOCK_INPUT", false);
          }
          if (msg.text.includes(state.variable)) {
            msg.text = msg.text.replace(state.variable, state[state.variable]);
          }
          commit("SET_MESSAGES_TO_STATE", msg);
          return msg;
        })
        .then(() => {
          commit("LOADER_HIDE");
          dispatch("scroll");
        })
        .catch(() => commit("LOADER_HIDE"));
    },
    scroll() {
      let container = document.querySelector(".container-scrollable"),
        list = document.querySelector(".screen-list");
      setTimeout(() => (container.scrollTop = list.scrollHeight), 50);
    }
  },
  getters: {
    GET_MESSAGES(state) {
      return state.messages;
    },
    VISIBLE: state => state.messages[state.messages.length - 1].visible,
    GET_ANSWER: state => state.answer,
    PLACE_HOLDER: state => state.placeholder,
    GET_MESSAGE(state) {
      let message;
      state.messages.forEach(msg => {
        if (msg.variable.name) {
          message = msg;
        }
      });
      return message;
    },
    LOADING_ON: state => state.loading,
    LOCK: state => state.lockInput
  }
});
