<template>
  <div class="container">
    <div class="container-scrollable">
      <transition-group class="screen-list" tag="ul" name="screen-appear">
        <li
          class="screen-list__screen"
          v-for="(message, index) of GET_MESSAGES"
          :key="index"
        >
          <!--<transition name="screen-appear">-->
          <screen-item
            v-if="message"
            :screen="message"
            :index="index"
            :key="index"
          ></screen-item>
          <!--</transition>-->

          <!--<transition name="to-left">-->
          <div class="msg-box msg-answer slide-left" v-if="message.answer">
            <span v-html="message.answer"></span>
          </div>
          <!--</transition>-->
        </li>
      </transition-group>
    </div>
    <v-input />
  </div>
</template>

<script>
import ScreenItem from "./ScreenItem";
import VInput from "../VInput";
import { mapActions, mapGetters } from "vuex";

export default {
  name: "ScreenList",
  components: {
    ScreenItem,
    VInput
  },
  data() {
    return {};
  },
  mounted() {
    this.DOWNLOAD_MESSAGE_FROM_API([]);
  },
  computed: {
    ...mapGetters(["SCREEN_LIST", "GET_ANSWER", "GET_MESSAGES", "VISIBLE"]),
    answer() {
      return this.GET_MESSAGES.answer;
    }
  },
  methods: {
    ...mapActions([
      "GET_MESSAGES_FROM_API",
      "DOWNLOAD_MESSAGE_FROM_API",
      "scroll"
    ])
  }
};
</script>

<style lang="scss">
.container-scrollable {
  overflow-x: hidden;
  overflow-y: scroll;
  height: calc(100vh - 62px);
  &::-webkit-scrollbar {
    background: #f0f0f0;
    width: 10px;
    box-shadow: inset 0 0 5px 5px rgba(#000, 0.5);
    transition: 0.3s;
    &:hover {
      width: 15px;
    }
  }

  &::-webkit-scrollbar-track {
    background-color: #f0f0f0;
    box-shadow: inset 0 0 7px 2px rgba(0, 0, 0, 0.5);
  }
  &::-webkit-scrollbar-thumb {
    border-radius: 20px;
    width: 10px;
    background: linear-gradient(left, rgba(#000, 0.2), rgba(#000, 0.6));
    box-shadow: inset 0 0 1px 1px #5c6670;
    transition: 0.3s;
    &:hover {
      background: linear-gradient(left, rgba(#000, 0.5), rgba(#000, 0.8));
    }
  }
}

.screen-list {
  padding: 0 20px 30px;
  &__screen {
    margin-bottom: 35px;
    &:last-child {
      margin-bottom: 0;
    }
  }
}

.msg-answer {
  background: #cfe2fa;
  color: #000;
  margin: 20px 0 20px auto;
  min-height: 50px;
  span {
    position: relative;
    z-index: 10;
  }
}
</style>
