<template>
  <div class="flex-container">
    <div class="row" :key="'screen_one'">
      <div
        class="screen-bg"
        :class="[{ 'is-loading': loading }, animClass]"
        v-if="media"
      >
        <img :src="imgSrc" v-if="type_img" />
        <iframe
          v-else-if="type_video"
          class="screen-bg__video"
          style="height: 34vmax"
          :src="video_src"
          frameborder="0"
          allow="accelerometer; autoplay; encrypted-media; gyroscope; picture-in-picture"
          allowfullscreen
        ></iframe>
      </div>
    </div>
    <div class="row" :key="'screen_two'">
      <!--<transition name="to-right" appear>-->
      <div
        v-if="msgOut"
        class="msg-box msg-question slide-right"
        :style="customize"
        :key="screen.id"
      >
        <div class="msg-inner" v-html="msgOut"></div>
      </div>
      <!--</transition>-->
    </div>
    <div class="row" :key="'screen_three'">
      <!--<transition name="slide-top">-->
      <div class="btn-group" v-if="buttons">
        <v-button
          v-for="(button, index) of buttons"
          :key="index"
          class="slide-top"
          :style="[button.customize, { animationDelay: 120 * index + 'ms' }]"
          @click="next(screen, button)"
          :disabled="$store.state.loading || screen.clicked"
          v-html="tagReplacer(button.text)"
        >
        </v-button>
      </div>
      <!--</transition>-->
    </div>
  </div>
</template>

<script>
import VButton from "../VButton";
import { mapActions, mapGetters } from "vuex";

export default {
  components: {
    VButton
  },
  props: {
    screen: Object,
    index: Number
  },
  data() {
    return {
      imgSrc: null,
      loading: false,
      answer: null
    };
  },
  mounted() {
    this.imgLoading();
  },
  computed: {
    ...mapGetters(["GET_ANSWER", "VISIBLE"]),
    media() {
      if (this.screen.attacment) return true;
      else return false;
    },
    video_src() {
      if (this.screen.attacment.type === "youtube") {
        return `https://www.youtube.com/embed/${this.screen.attacment.data}`;
      }
      if (this.screen.attacment.type === "vimeo") {
        return `https://player.vimeo.com/video/${this.screen.attacment.data}`;
      }
    },
    buttons() {
      if (!this.screen.buttons.length) return false;
      return this.screen.buttons.map(btn => {
        return Object.assign(btn, {
          customize: {
            background: `#${btn.customize.background}`,
            color: `#${btn.customize.color}`
          }
        });
      });
    },
    customize() {
      return {
        background: `#${this.screen.customize.background}`,
        color: `#${this.screen.customize.color}`
      };
    },
    type_img() {
      if (!this.screen.attacment) return false;
      else if (this.screen.attacment.type === "image") {
        return true;
      } else return false;
    },
    type_video() {
      if (!this.screen.attacment) return false;
      else if (this.screen.attacment.type === "vimeo") {
        return true;
      } else return false;
    },
    msgOut() {
      let text = this.screen.text;
      text = text.replace(/\n/gm, '<br>')
      return this.tagReplacer(text)
    },
    animClass() {
      if (this.index === 0) {
        return "appear-tada";
      } else return "appear";
    }
  },
  methods: {
    ...mapActions(["setAnswer", "nextMessage", "scroll", "setVariable"]),
    tagReplacer(text) {
      if (!text) return;
      if (text.includes("[bold]")) {
        text = text
          .replace("[bold]", "<strong>")
          .replace("[/bold]", "</strong>")
      }
      if (text.includes("[em]")) {
        text = text
          .replace("[em]", "<em>")
          .replace("[/em]", "</em>")
      }
      return text;
    },
    next(screen, button) {
      let action = button.action,
        anchor = button[action],
        answer = this.tagReplacer(button.text);
      if (action === "link") {
        window.open(anchor);
      }
      let variable = {
        name: button.variable.name,
        value: button.variable.value
      };
      this.nextMessage([screen, variable, answer, anchor, action]);
      screen.clicked = true;
    },
    imgLoading() {
      if (!this.screen.attacment) return false;
      this.loading = true;
      /* new Promise(resolve => {
        this.loading = false;
        this.imgSrc = this.screen.attacment.thumbnail;
        console.log(this.imgSrc)
        resolve();
      })
      .then(() => {
        this.imgSrc = this.screen.attacment.data;
        console.log(this.imgSrc)
      })*/
      this.imgSrc = this.screen.attacment.thumbnail;
      setTimeout(() => {
        this.imgSrc = this.screen.attacment.data;
        this.loading = false;
      }, 400);
    }
  }
};
</script>

<style lang="scss">
.screen-bg {
  width: 100%;
  display: flex;
  flex-direction: column;
  align-items: stretch;
  min-height: 100%;
  overflow: hidden;
  img {
    width: 100%;
  }
  &__video {
    width: 100%;
    .vp-video {
      background: #000;
    }
  }
  .loading {
    filter: blur(8px);
  }
}

.btn-group {
  margin: 20px 0;
  .screen-btn {
    opacity: 0;
    transform: translateY(50%);
  }
}

.msg-container {
  display: flex;
  width: 100%;
  justify-content: flex-end;
}

.msg-box {
  position: relative;
  width: 45%;
  border-radius: 7px;
  padding: 10px 5px 10px 15px;
  line-height: 1.5;
  transition: 0.3s;
  &:after {
    content: "";
    position: absolute;
    top: 0;
    width: 54px;
    height: 13px;
    background: inherit;
    /*border: 13px solid transparent;*/
  }
  &:before {
    content: "";
    display: block;
    position: absolute;
    top: 0;
    z-index: 10;
    height: 20px;
    width: 20px;
    background: #000;
  }
  &.msg-question {

    &:before {
      left: -13px;
      transform: translate(-80%, 62%) rotate(-45deg);
      transform-origin: 0 0;
    }
    &:after {
      /*border-top-color: inherit;*/
      left: -13px;
    }
  }

  &.msg-answer {
    &:before {
      right: -13px;
      transform: translate(41%, 62%) rotate(-45deg);
      transform-origin: 0 0;
    }
    &:after {
      right: -13px;
    }
    /*border-top-color: inherit;*/
  }
  @media screen and (max-width: 768px) {
    width: 90%;
  }
}

.msg-question {
  background: #f0f0f0;
  margin-top: 15px;
}

.msg-inner {
  padding: 7px 20px 7px 0;
  p {
    margin-bottom: 20px;
    &:last-child {
      margin-bottom: 0;
    }
  }
}
</style>
